import React from 'react'
import './maincontainer.css'
import AboutUs from './content/AboutUs'
import Book from './content/Book'
import Stuff from './content/Stuff'

const MainContainer = ({setShowMain, setShowBooking, setShowPricing, setShowRules, setShowSmaller, setShowBigger, setShowStudio}) => {
    return (
        <div className="main-container section">
            <Book 
            setShowMain={setShowMain} 
            setShowBooking={setShowBooking} 
            setShowPricing={setShowPricing} 
            setShowRules={setShowRules} 
            setShowSmaller={setShowSmaller}
            setShowBigger={setShowBigger}
            setShowStudio={setShowStudio}
            />
            <AboutUs/>
            <Stuff/>
        </div>
    )
}

export default MainContainer
