import React, {useState} from 'react'
import './navbar.css'
import NavbarLink from './navlink/NavbarLink'
import {NavLink } from 'react-router-dom'

const Navbar = ({setShowMain, setShowBooking, setShowPricing, setShowRules, setShowBigger, setShowSmaller, setShowStudio}) => {

    return (
        <div className="navbar">
            <div className="navbar-inside">
                <NavLink 
                    to="/rezerwacje" 
                    activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}
                    >
                    <NavbarLink text={"Rezerwacje"} onClick={()=>{
                    setShowBooking(true)
                    setShowMain(false)
                    setShowPricing(false)
                    setShowRules(false)
                    setShowBigger(false)
                    setShowSmaller(false)
                    setShowStudio(false)
                }}/>
                </NavLink>
                <NavLink to="/sala_mniejsza" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <NavbarLink text={"Sala mniejsza"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(true)
                     setShowStudio(false)
                }}/>
                </NavLink>
                <NavLink to="/sala_wieksza" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <NavbarLink text={"Sala wiÄ™ksza"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(true)
                     setShowSmaller(false)
                     setShowStudio(false)
                }}/>
                </NavLink>
                <NavLink to="/studio_nagran" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <NavbarLink text={"Studio"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowStudio(true)
                }}/>
                </NavLink>
                <NavLink to="/cennik" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <NavbarLink text={"Cennik"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(true)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowStudio(false)
                }}/>
                </NavLink>
                <NavLink to="/regulamin" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                <NavbarLink text={"Regulamin"} onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(true)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowStudio(false)
                }}/>
                </NavLink>
                
            </div>
        </div>
    )
}

export default Navbar
