import React from 'react'
import Button from './Button'
import { NavLink } from 'react-router-dom'

const Book = ({setShowMain, setShowBooking, setShowPricing, setShowRules, setShowSmaller, setShowBigger, setShowStudio}) => {
    return (
        <div className="about-us-booking">
                <div className="about-us-booking-header"><h2>Profesjonalne sale prÃ³b</h2></div>
                <div className="about-us-booking-content" >
                    <NavLink to="/rezerwacje" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                    <Button text="Rezerwacje" onClick={()=>{
                     setShowBooking(true)
                     setShowMain(false)
                     setShowPricing(false)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowStudio(false)
                        }}/>
                    </NavLink>
                    <NavLink to="/cennik" activeStyle={{
                        textDecoration: "underline",
                        fontWeight: "bold"
                    }}>
                    <Button text="Cennik" onClick={()=>{
                     setShowBooking(false)
                     setShowMain(false)
                     setShowPricing(true)
                     setShowRules(false)
                     setShowBigger(false)
                     setShowSmaller(false)
                     setShowStudio(false)
                }}/>
                </NavLink>
                </div>
            </div>
    )
}

export default Book
